.main_div {
  width: 100%;
  height: 100vh;
  text-align: center;
  background: #6983aa;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.center_div {
  width: 25%;
  height: 80vh;
  background-color: #f4f4f4;
  border-radius: 15px;
  box-shadow: 5px 5px 25px -5px #00000080;
}

h1 {
  color: #fff;
  background-color: #8566aa;
  margin-bottom: 10px;
  padding: 6px 0 2px;
  box-shadow: 5px 5px 15px -5px #0000004d;
}

button:hover {
  background-color: #20bf6b;
}

ol {
  margin-top: 30px;
}

ol li {
  text-align: left;
  min-height: 40px;
  color: #8566aa;
  text-transform: capitalize;
  align-items: center;
  padding-left: 0;
  font-size: 20px;
  font-weight: 500;
  display: flex;
}

.todo_style {
  flex-direction: row;
  justify-content: left;
  align-items: center;
  display: flex;
}

.todo_style .fa-times {
  width: 20px;
  height: 20px;
  color: #f0f8ff;
  background-color: #8566aa;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 0 15px 0 35px;
  display: flex;
  box-shadow: 5px 5px 15px -5px #0000004d;
}

.todo_style .fa-times:hover {
  background-color: red;
}

@media (width <= 768px) {
  .center_div {
    width: 55%;
  }

  .todo_style .fa-times {
    justify-content: center;
    align-items: center;
    margin: 0 15px;
    display: flex;
  }

  button {
    min-height: 40px;
    width: 40px;
    color: #fff;
    margin-left: 5px;
    font-size: 30px;
  }
}

